import React from 'react';
import { WhiteSpace, InputItem, Checkbox } from 'antd-mobile';
import { Page, PageTitle, PrimaryButton, OthersItem } from '../styles';

const { AgreeItem } = Checkbox;

const Register: React.FC = () => {
  return (
    <Page>
      <PageTitle>新用户注册</PageTitle>
      <InputItem placeholder="请输入邮箱" />
      <WhiteSpace size="lg" />
      <InputItem placeholder="请输入用户名" />
      <WhiteSpace size="lg" />
      <InputItem placeholder="请输入密码" type="password" />
      <WhiteSpace size="lg" />
      <InputItem placeholder="请输入确认密码" type="password" />
      <WhiteSpace size="lg" />
      <PrimaryButton type="primary">注册</PrimaryButton>
      <AgreeItem
        data-seed="logId"
        onChange={(e: TouchEvent): void => console.log('checkbox', e)}
      >
        我同意
        <OthersItem
          onClick={(e): void => {
            e.preventDefault();
            alert('agree 用户服务协议');
          }}
        >
          《用户服务协议》
        </OthersItem>
        ,
        <OthersItem
          onClick={(e): void => {
            e.preventDefault();
            alert('agree 隐私政策');
          }}
        >
          《隐私政策》
        </OthersItem>
      </AgreeItem>
    </Page>
  );
};

export default Register;
